<h3>自定义样式</h3>
<div class="demo-block">
  <div>
    <xm-tabs type="vertical" :dataSource="param" v-model="selected" @change="change" className="xm-tabs-custom"></xm-tabs>
  </div>
</div>
<demo-block>
                        <div slot="desc">

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span>&gt;</span>
