<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>dataSource</td>
<td>选择的数据</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>keyName</td>
<td>自定义数据的key字段名</td>
<td>String</td>
<td>-</td>
<td>key</td>
</tr>
<tr>
<td>titleName</td>
<td>自定义数据的title字段名</td>
<td>String</td>
<td>—</td>
<td>value</td>
</tr>
<tr>
<td>value v-model</td>
<td>选中的值</td>
<td>Object String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>className</td>
<td>自定义样式</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>tabs水平或者是垂直方向</td>
<td>String</td>
<td>default,vertical</td>
<td>default</td>
</tr>
</tbody>
</table>
<h2>Tabs 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>切换Tab触发的事件</td>
<td>object</td>
</tr>
<tr>
<td>click</td>
<td>点击Tab触发的事件</td>
<td>object</td>
</tr>
<tr>
<td>slot插槽</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="less">
.xm-tabs-custom{
  >div {
    display: block;
    padding: 12px 16px;
    line-height:1;
    font-size: 15px;
    &:hover,&.xm-tabs__item--selected{
      border-left: 2px solid #3199F5;
      background: #EBF5FF;;
    }
  }
}

</style><style lang="less">
.xm-tabs-custom{
  >div {
    display: block;
    padding: 12px 16px;
    line-height:1;
    font-size: 15px;
