      border-left: 2px solid #3199F5;
      background: #EBF5FF;;
    }
  }
}

</style>
<script>
export default {
  data() {
    return {
         param: [
                {key: '全部',value:"全部"},
                {key: '模版',value:"模版"},
                {key: '文本',value:"文本"}
              ],
         list :[
              {id: '全部(1)',name:"全部"},
              {id: '模版(2)',name:"模版"},
              {id: '文本(3)',name:"文本"}
            ],
              selected: '模版',
              checked:"全部(1)"

     }
  },
  methods:{
   change(value){
      console.log(value)

   }

  }

}

</script>


