<template><section><h1>TextEllipsis 显示更多</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
  <div>
  <xm-text-ellipsis height="40" :text="text" :useTooltip="true">
      <template slot="more">....</template>
  </xm-text-ellipsis>
  </div>
</div>

<demo-block>
                        <div slot="desc">

<div class="demo-block">
  <xm-text-ellipsis height="40" :text="text">
      <template slot="more">.....</template>
  </xm-text-ellipsis>
</div>



</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">

<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xm-text-ellipsis</span>  <span class="hljs-attr">height</span>=<span class="hljs-string">"40"</span> <span class="hljs-attr">:text</span>=<span class="hljs-string">"text"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"more"</span>&gt;</span>.....<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xm-text-ellipsis</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {

      <span class="hljs-attr">text</span>: <span class="hljs-string">'据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者'</span>,
      <span class="hljs-attr">isLimitHeight</span>:<span class="hljs-literal">true</span>
    };
  }

};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>自定义前缀后缀</h3>
<div class="demo-block">
  <div>
  <xm-text-ellipsis height="40" :text="text">
     <span slot="before" class="xm-tag--danger xm-tag">new</span>
      <template slot="more">....</template>
      <span slot="after">[09/18]</span>
  </xm-text-ellipsis>
  </div>
</div>
<demo-block>
                        <div slot="desc">

<div class="demo-block">
  <xm-text-ellipsis height="40" :text="text">
      <span slot="before" class="xm-tag--danger xm-tag">new</span>
      <template slot="more">.....</template>
      <span slot="after">[09/18]</span>

  </xm-text-ellipsis>
</div>



</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">

<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xm-text-ellipsis</span>  <span class="hljs-attr">height</span>=<span class="hljs-string">"40"</span> <span class="hljs-attr">:text</span>=<span class="hljs-string">"text"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>  <span class="hljs-attr">slot</span>=<span class="hljs-string">"before"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"xm-tag--danger xm-tag"</span>&gt;</span>new<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"more"</span>&gt;</span>.....<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"after"</span>&gt;</span>[09/18]<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>

  <span class="hljs-tag">&lt;/<span class="hljs-name">xm-text-ellipsis</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">text</span>: <span class="hljs-string">'据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者'</span>
    };
  },

};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>自定义查看更多</h3>
<div class="demo-block">
  <div>
  <xm-text-ellipsis :isLimitHeight="isLimitHeight" height="40" :text="text">
      <template slot="more"><span @click="show">{{isLimitHeight?'查看更多':'收起'}}</span></template>
  </xm-text-ellipsis>
  </div>
</div>
<demo-block>
                        <div slot="desc">
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">text</span>: <span class="hljs-string">'据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者'</span>,
      <span class="hljs-attr">isLimitHeight</span>:<span class="hljs-literal">true</span>
    };
  },
    <span class="hljs-attr">methods</span>:{
      show(){

       <span class="hljs-keyword">this</span>.isLimitHeight=!<span class="hljs-keyword">this</span>.isLimitHeight

      }


    }

};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>text</td>
<td>需要省略的文本</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>height</td>
<td>限制的高度</td>
<td>String,Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>row</td>
<td>限制行数与限制高度不能同时写 只能写一个</td>
<td>String,Number</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>isLimitHeight</td>
<td>是否开启限制</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>textStyle</td>
<td>文字的style</td>
<td>String, Object, Array</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>textClass</td>
<td>文字的class</td>
<td>String, Object, Array</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>more</td>
<td>省略文字后面</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>useTooltip</td>
<td>是否使用tooltip</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>TextEllipsis 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>当isLimitHeight为true，文本全部展示的时候</td>
<td>event</td>
</tr>
<tr>
<td>hide</td>
<td>当isLimitHeight为true，文本省略的时候</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      text: '据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者据青岛市卫健委通报，截至10月13日8时，青岛市已采样3078528份进行核酸检测，未发现新增阳性感染者',
      isLimitHeight:true
    };
  },
    methods:{
      show(){

       this.isLimitHeight=!this.isLimitHeight

      }


    }

};
</script>


