              },
              {
                <span class="hljs-attr">key</span>: <span class="hljs-string">'测试3'</span>,
                <span class="hljs-attr">value</span>: <span class="hljs-string">'测试3'</span>
              }

            ]
          },
          {
              <span class="hljs-attr">key</span>: <span class="hljs-string">'菜单二'</span>,
              <span class="hljs-attr">value</span>: <span class="hljs-string">'菜单二'</span>,
