                {
                  <span class="hljs-attr">key</span>: <span class="hljs-string">'测试4'</span>,
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'测试4'</span>
                },
                {
                  <span class="hljs-attr">key</span>: <span class="hljs-string">'测试5'</span>,
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'测试5'</span>
                   },
                {
                  <span class="hljs-attr">key</span>: <span class="hljs-string">'测试6'</span>,
                  <span class="hljs-attr">value</span>: <span class="hljs-string">'测试6'</span>
                   }

              ]
            },
             {
                 <span class="hljs-attr">key</span>: <span class="hljs-string">'菜单二'</span>,
                 <span class="hljs-attr">value</span>: <span class="hljs-string">'菜单二'</span>,
                 <span class="hljs-attr">menus</span>: [
                   {
                     <span class="hljs-attr">key</span>: <span class="hljs-string">'测试4'</span>,
                     <span class="hljs-attr">value</span>: <span class="hljs-string">'测试4'</span>
                   },
                   {
                     <span class="hljs-attr">key</span>: <span class="hljs-string">'测试5'</span>,
                     <span class="hljs-attr">value</span>: <span class="hljs-string">'测试5'</span>
                      },
                   {
                     <span class="hljs-attr">key</span>: <span class="hljs-string">'测试6'</span>,
                     <span class="hljs-attr">value</span>: <span class="hljs-string">'测试6'</span>
                      }

