    }
  },
}

</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>





</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>key</td>
<td>菜单文本</td>
<td>String</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>菜单值，方法回调返回值</td>
<td>Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>menus</td>
<td>菜单列表</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>FixedMenu 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>handMenuClick</td>
<td>点击菜单回调</td>
<td>value 菜单值</td>
</tr>
<tr>
<td>handMenuItemClick</td>
<td>点击子菜单回调</td>
<td>value 菜单值</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {

  data() {
    return {
