<template><section>
<h1>limit 限制输入域</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
  <div>
   <xm-limit type="textarea" v-model="content" limit="20" placeholder="最多输入10个字符"></xm-limit>
    <xm-limit v-model="content" limit="20" placeholder="最多输入10个字符"></xm-limit>
  </div>
</div>
<demo-block>
                        <div slot="desc">

<div>
   <xm-limit type="textarea" v-model="content" limit="20" placeholder="最多输入10个字符">

   </xm-limit>

   <xm-limit v-model="content" limit="20" placeholder="最多输入10个字符">

   </xm-limit>

</div>


</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">

<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">xm-limit</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">limit</span>=<span class="hljs-string">"20"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最多输入10个字符"</span>&gt;</span>

   <span class="hljs-tag">&lt;/<span class="hljs-name">xm-limit</span>&gt;</span>

   <span class="hljs-tag">&lt;<span class="hljs-name">xm-limit</span>  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">limit</span>=<span class="hljs-string">"20"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最多输入10个字符"</span>&gt;</span>

   <span class="hljs-tag">&lt;/<span class="hljs-name">xm-limit</span>&gt;</span>

<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>


</code></pre>
</div></demo-block>

<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>rows</td>
<td>列高</td>
<td>Number</td>
<td>—</td>
<td>4</td>
</tr>
<tr>
<td>limit</td>
<td>Number</td>
<td>长度限制</td>
<td>-</td>
<td>20</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入时需要显示的提示文案</td>
<td>String</td>
<td>—</td>
<td>请输入内容</td>
</tr>
</tbody>
</table>
<h2>Limit 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>overText</td>
<td>超出限制长度触发</td>
<td>content</td>
</tr>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>value</td>
</tr>
<tr>
<td>其他属性</td>
<td>其他属性参考elementUI</td>
<td>value</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .xm-limit{

    margin: 10px;
  }
</style>
<script>
export default {
  data() {
    return {
      content: ''
    };
  }
}
</script>


